import produce from "immer";
import { useEffect, useState } from "react";
import { calculate, rollSim } from "./calculator";
import RollConfig from "./components/RollConfig";
import ArtsConfig from "./components/ArtsConfig";
import CharAttr from "./components/CharAttt";
import CharConfig from "./components/CharConfig";
import { ExpandableList } from "./components/Common";
import ModsConfig from "./components/ModsConfig";
import WpConfig from "./components/WpConfig";
import { countAvailableRolls } from "./helpers";
import "./styles.css";
import acro from "./convertAcronym";
import Modifiers from "./components/Modifiers";
import SimResults from "./components/SimResults";
import Notes from "./components/Notes";
import { Settings } from "./components/styles";

const { hp_, er, hydroD, cRate, cDmg, hydroRd } = acro;

export default function App() {
  const [char, setChar] = useState({
    level: "90/90",
    CA: 10,
    ES: 10,
    EB: 10
  });
  const [wp, setWp] = useState({
    name: "Aqua Simulacra",
    level: "90/90",
    refinement: 1
  });
  const [artSets, setArtSets] = useState(["4P EoSF", null]);
  const [artStats, setArtStats] = useState({
    Flower: { rolls: 9 },
    Plume: { rolls: 9 },
    Sands: { type: hp_, rolls: 8 },
    Goblet: { type: hydroD, rolls: 9 },
    Circlet: { type: cRate, rolls: 8 }
  });
  const [mods, setMods] = useState({
    [hp_]: 0,
    [er]: 0,
    [hydroD]: 0,
    CA: 0,
    ES: 0,
    EB: 0,
    [hydroRd]: 0
  });
  const [rollConfig, setRollConfig] = useState({
    [er]: { value: "6.48", rolls: 0, total: 0 },
    [hp_]: "5.83",
    [cRate]: "3.89",
    [cDmg]: "7.77"
  });
  const [simResults, setSimResults] = useState(null);

  const avaRolls = countAvailableRolls(artStats);
  const maxSimRolls = avaRolls.total - rollConfig[er].rolls;
  const avaHpRolls = Math.min(maxSimRolls, avaRolls.hp);
  const [ATTRs, bonuses, resMult] = calculate(
    char,
    wp,
    artStats,
    artSets,
    rollConfig[er].total,
    mods
  );

  useEffect(() => {
    if (avaRolls.er < rollConfig[er].rolls) {
      setRollConfig(
        produce((draft) => {
          draft[er].rolls = avaRolls.er;
        })
      );
    }
  }, [avaRolls.er]);

  const startSim = () => {
    const results = rollSim(
      avaHpRolls,
      ATTRs,
      rollConfig,
      maxSimRolls,
      char,
      bonuses,
      resMult
    );
    setSimResults(results);
  };

  return (
    <div className="App">
      <Settings>
        <div className="p-2 static_config">
          <h1 className="mb-2">Yelan</h1>
          <div>
            <ExpandableList
              headingList={[
                "Character Config",
                "Weapon Config",
                "Artifacts Config",
                "Modifiers Config"
              ]}
              contentList={[
                <CharConfig char={char} setChar={setChar} />,
                <WpConfig wp={wp} setWp={setWp} />,
                <ArtsConfig
                  artStats={artStats}
                  artSets={artSets}
                  setArtStats={setArtStats}
                  setArtSets={setArtSets}
                />,
                <ModsConfig mods={mods} setMods={setMods} />
              ]}
            />
          </div>
        </div>
        <div className="flex-col">
          <div className="flex-wrap">
            <RollConfig
              avaRolls={avaRolls}
              avaHpRolls={avaHpRolls}
              rollConfig={rollConfig}
              setRollConfig={setRollConfig}
            />
            <CharAttr ATTRs={ATTRs} />
            <Modifiers
              bonuses={bonuses}
              level={char.level}
              res={mods[hydroRd]}
              resMult={resMult}
              startSim={startSim}
            />
          </div>
        </div>
      </Settings>
      <SimResults
        ATTRs={ATTRs}
        bonuses={bonuses}
        resMult={resMult}
        char={char}
        results={simResults}
      />
      <Notes />
    </div>
  );
}
