import { levels } from "./data/misc";

export function mapToLevels(arr) {
  const result = {};
  levels.forEach((lv, i) => {
    result[lv] = arr[i];
  });
  return result;
}

export const pct = (type) =>
  ["atk_", "hp_", "er", "hydro", "cRate", "cDmg"].includes(type) ? "%" : "";

export function processNumInp(inp, before, max, min = 0) {
  if (inp === "") return 0;
  if (!isNaN(inp) && inp >= min && inp <= max) {
    if (inp.slice(-1) === ".") return inp;
    return Math.round(inp * 10) / 10;
  }
  return before;
}

export function countAvailableRolls(arts) {
  let total = 0;
  let hp = 0;
  let er = 0;
  for (const artType of Object.keys(arts)) {
    const { type, rolls } = arts[artType];
    total += rolls;
    if (type !== "HP%") {
      hp += Math.min(6, rolls);
    }
    if (type !== "Energy Recharge") {
      er += Math.min(6, rolls);
    }
  }
  return { total, hp, er };
}

export function getRandomN(min, max, step = 1) {
  const range = max - min + 1;
  return min + Math.floor(Math.random() * range);
}

export const round2 = (n) => Math.round(n * 100) / 100;

export const round4 = (n) => Math.round(n * 10000) / 10000;
