import acro from "../convertAcronym";
import { levels } from "../data/misc";
import { VStack } from "./styles";

export default function CharConfig({ char, setChar }) {
  return (
    <VStack className="px-2 py-3">
      <div className="line-1">
        <p>Level</p>
        <select
          value={char.level}
          onChange={(e) =>
            setChar((prev) => ({ ...prev, level: e.target.value }))
          }
        >
          {levels.map((lv) => (
            <option key={lv}>{lv}</option>
          ))}
        </select>
      </div>
      {["CA", "ES", "EB"].map((type) => (
        <div key={type} className="line-1">
          <p>{acro[type]}</p>
          <select
            value={char[type]}
            onChange={(e) =>
              setChar((prev) => ({ ...prev, [type]: e.target.value }))
            }
          >
            {[...Array(15).keys()].map((_, i) => (
              <option key={i}>{i + 1}</option>
            ))}
          </select>
        </div>
      ))}
    </VStack>
  );
}
