import acro from "../convertAcronym";
import { processNumInp } from "../helpers";
import { Pct } from "./Common";
import { VStack } from "./styles";

export default function ModsConfig({ mods, setMods }) {
  return (
    <VStack className="px-2 py-3">
      {Object.keys(mods).map((type) => {
        const tlBn = acro[type];
        return (
          <div key={type} className="line-1">
            <p>{tlBn ? tlBn + ` DMG Bonus` : type}</p>
            <input
              value={mods[type]}
              onChange={(e) =>
                setMods((prev) => ({
                  ...prev,
                  [type]: processNumInp(e.target.value, mods[type], 9999)
                }))
              }
            />
            <Pct />
          </div>
        );
      })}
    </VStack>
  );
}
