import { ExpandableList } from "./Common";
import { Span } from "./styles";
import acro from "../convertAcronym";
import Yelan from "../data/Yelan";
import { TalentLvMults } from "../data/char.config";
import { round2, round4 } from "../helpers";

const { hp_, cRate, cDmg } = acro;
const mults = Yelan.actvTalents.map(({ baseMult }) => baseMult);

const Dot = () => <Span color="blue">•</Span>;

export default function SimResults({ ATTRs, bonuses, resMult, char, results }) {
  if (!results) return null;
  const { hpRS, cRateRS, cDmgRS, dmg, states } = results;
  const arr = [...Array(dmg.length).keys()].map((_, i) => i);

  const mult = {};
  ["CA", "ES", "EB"].forEach((type, i) => {
    mult[type] =
      Math.round(mults[i] * TalentLvMults[2][char[type]] * 100) / 10000;
  });

  const headingList = arr.map((i) => (
    <Span>
      <b>{hpRS[i].length}</b> HP% rolls.{" "}
      <b>{cRateRS[i].length + cDmgRS[i].length}</b> Crit rolls. Average DMG:
      <b>{dmg[i].CA}</b> / <b>{dmg[i].ES}</b> / <b>{dmg[i].EB}</b>
    </Span>
  ));
  return (
    <div className="mt-3">
      <h2 className="mb-3">Roll Results</h2>
      <ExpandableList
        headingList={headingList}
        contentList={arr.map((i) => (
          <Content
            key={i}
            hpRolls={hpRS[i]}
            cRateRolls={cRateRS[i]}
            cDmgRolls={cDmgRS[i]}
            ATTRs={ATTRs}
            bonuses={bonuses}
            resMult={resMult}
            mult={mult}
            state={states[i]}
          />
        ))}
      />
    </div>
  );
}

function Content({
  hpRolls,
  cRateRolls,
  cDmgRolls,
  ATTRs,
  bonuses,
  resMult,
  mult,
  state
}) {
  const hpPct = round4(1 + state[hp_] / 100);
  const hp = Math.round(ATTRs["Base HP"] * hpPct) + ATTRs.HP;
  const CR = round4(state[cRate] / 100);
  const CD = round4(state[cDmg] / 100);
  const critMult = round4(1 + CR * CD);
  return (
    <div className="p-1">
      <p>
        <Dot /> HP% rolls ({hpRolls.length}): [{hpRolls.join(", ")}]
      </p>
      <p>
        <Dot /> Crit Rate rolls ({cRateRolls.length}): [{cRateRolls.join(", ")}]
      </p>
      <p>
        <Dot /> Crit DMG rolls ({cDmgRolls.length}): [{cDmgRolls.join(", ")}]
      </p>
      <p>
        <Dot /> HP = {ATTRs["Base HP"]} * {hpPct} + {ATTRs.HP} = <b>{hp}</b>
      </p>
      <p>
        <Dot /> CRIT Rate = <b>{round2(state[cRate])}%</b> | CRIT DMG ={" "}
        <b>{round2(state[cDmg])}%</b>
      </p>
      <p>
        <Dot /> CRIT Multiplier = 1 + {CR} * {CD} = <b>{critMult}</b>
      </p>
      {["CA", "ES", "EB"].map((type) => {
        const dmgBn = 1 + bonuses[type] / 100;
        return (
          <p key={type}>
            <Dot /> {acro[type]} Average DMG: <b>{hp}</b> (hp) *{" "}
            <b>{mult[type]}</b> (talent) * <b>{dmgBn}</b> (dmg bonus) *{" "}
            <b>{critMult}</b> (crit) * <b>0.5</b> (def) * <b>{resMult}</b> (res)
            ={" "}
            <b>
              {Math.round(hp * mult[type] * dmgBn * critMult * 0.5 * resMult)}
            </b>
          </p>
        );
      })}
    </div>
  );
}
