import bows from "../data/bows";
import { levels } from "../data/misc";
import { VStack } from "./styles";

export default function WpConfig({ wp, setWp }) {
  const wpData = bows.find(({ name }) => name === wp.name);
  return (
    <VStack className="px-2 py-3">
      <div className="line-1">
        <p>Name</p>
        <select
          value={wp.name}
          onChange={(e) => setWp((prev) => ({ ...prev, name: e.target.value }))}
        >
          {bows.map(({ name }) => (
            <option key={name}>{name}</option>
          ))}
        </select>
      </div>
      <div className="line-1">
        <p>Level</p>
        <select
          value={wp.level}
          onChange={(e) =>
            setWp((prev) => ({ ...prev, level: e.target.value }))
          }
        >
          {levels.map((lv) => (
            <option key={lv}>{lv}</option>
          ))}
        </select>
      </div>
      <div className="line-1">
        <p>Refinement</p>
        <select
          value={wp.refinement}
          onChange={(e) =>
            setWp((prev) => ({ ...prev, refinement: +e.target.value }))
          }
        >
          {[...Array(5).keys()].map((_, i) => (
            <option key={i}>{i + 1}</option>
          ))}
        </select>
      </div>
      <p>{wpData?.desc(wp.refinement)}</p>
    </VStack>
  );
}
