import produce from "immer";
import acro from "../../convertAcronym";
import { rollValues } from "../../data/misc";
import { Card, Pct } from "../Common";
import ErRoll from "./ErRoll";

const { hp_, cRate, cDmg } = acro;

export default function ArtRollConfig({
  avaRolls,
  avaHpRolls,
  rollConfig,
  setRollConfig
}) {
  const changeRollValue = (type) => (e) =>
    setRollConfig(
      produce((draft) => {
        draft[type] = e.target.value;
      })
    );

  return (
    <Card>
      <h2>Roll Config</h2>
      <p>
        Total rolls available: <b>{avaRolls.total}</b>
      </p>
      <ErRoll
        avaRolls={avaRolls}
        rollConfig={rollConfig}
        setRollConfig={setRollConfig}
      />
      <p>
        HP% rolls available: <b>{avaHpRolls}</b>
      </p>
      {[hp_, cRate, cDmg].map((type) => (
        <div key={type} className="line-1">
          <p>{type} roll value</p>
          <select value={rollConfig[type]} onChange={changeRollValue(type)}>
            {[...rollValues[type], "Random"].map((n) => (
              <option key={n}>{n}</option>
            ))}
          </select>
          <Pct />
        </div>
      ))}
    </Card>
  );
}
