import { round2 } from "../helpers";
import { Card } from "./Common";

const fields = ["Energy Recharge", "CRIT Rate", "CRIT DMG", "Hydro DMG Bonus"];

export default function CharAttr({ ATTRs }) {
  return (
    <Card>
      <h2>Attributes</h2>
      {["HP", "ATK"].map((type) => {
        const base = ATTRs[`Base ${type}`];
        const flatBn = ATTRs[type];
        const pctBn = ATTRs[`${type}%`];
        const calcPctBn = Math.round((base * pctBn) / 100);
        let value = base.toString();
        if (flatBn) value += ` + ${flatBn}`;
        if (pctBn) value += ` + ${calcPctBn} (${pctBn}%)`;
        return (
          <div key={type} className="line-1">
            <p>
              {type}: {value}
              {(flatBn || pctBn) && (
                <>
                  {" "}
                  = <b>{base + flatBn + calcPctBn}</b>
                </>
              )}
            </p>
          </div>
        );
      })}
      {fields.map((type) => {
        return (
          <div key={type} className="line-1">
            <p>
              {type}: <b>{round2(ATTRs[type])}%</b>
            </p>
          </div>
        );
      })}
    </Card>
  );
}
