import acro from "../convertAcronym";
import { Card } from "./Common";
import { VStack } from "./styles";
import { round2 } from "../helpers";

export default function Modifiers({ bonuses, level, res, resMult, startSim }) {
  const lv = +level.slice(0, 2);
  return (
    <Card>
      <VStack space="0.5rem">
        <h2>Total DMG Bonus</h2>
        {Object.keys(bonuses).map((type) => (
          <p key={type}>
            {acro[type]}: <b>{round2(bonuses[type])}%</b>
          </p>
        ))}
      </VStack>
      <VStack space="0.5rem">
        <h2>DEF Multiplier</h2>
        <p>Assume target's level is equal to Yelan.</p>
        <p>
          ({lv} + 100) / ({lv} + {lv} + 200) = <b>0.5</b>
        </p>
      </VStack>
      <VStack space="0.5rem">
        <h2>Hydro RES Multiplier</h2>
        <p>Assume target's Hydro RES is 10%.</p>
        <p>
          RES after reduction: 10 - {res} = {10 - res}%
        </p>
        <p>Multiplier: {ResMultDisplayer(10 - res, resMult)}</p>
      </VStack>
      <button className="full-w pt-2" onClick={startSim}>
        ROLL
      </button>
    </Card>
  );
}

function ResMultDisplayer(res, resMult) {
  const RES = res / 100;
  if (RES < 0) {
    const result = 1 - RES / 2;
    return (
      <>
        1 - ({RES}) / 2 ={" "}
        <b>
          {result} {result !== resMult && "!!!"}
        </b>
      </>
    );
  } else if (RES >= 0.75) {
    const result = 1 / (4 * RES + 1);
    return (
      <>
        1 / (4 * {RES} + 1) ={" "}
        <b>
          {result} {result !== resMult && "!!!"}
        </b>
      </>
    );
  }
  const result = 1 - RES;
  return (
    <>
      1 - {RES} ={" "}
      <b>
        {result} {result !== resMult && "!!!"}
      </b>
    </>
  );
}
