import acro from "./convertAcronym";
import bows from "./data/bows";
import Yelan from "./data/Yelan";
import { TalentLvMults } from "./data/char.config";
import { rollValues } from "./data/misc";
import { round2, getRandomN } from "./helpers";

const { hp_, er, hydroD, cRate, cDmg, hydroRd } = acro;

function addCharStats(ATTRs, char) {
  const stats = Yelan.stats[char.level];
  for (const type of Object.keys(stats)) {
    if (ATTRs[type] !== undefined) {
      ATTRs[type] += stats[type];
    }
  }
}

function addWpStats(ATTRs, bonuses, wp) {
  const wpData = bows.find(({ name }) => name === wp.name);
  ATTRs.ATK += wpData.mainStat.value[wp.level];
  const { type, value } = wpData.subStats[0];
  if (ATTRs[type] !== undefined) {
    ATTRs[type] += value[wp.level];
  }
  if (wpData.addBnes) {
    wpData.addBnes({ ATTRs, bonuses, wpRfm: wp.refinement });
  }
}

function addArtStats(ATTRs, artStats) {
  ATTRs.HP += 4780;
  ATTRs.ATK += 311;
  for (const artType of ["Sands", "Goblet", "Circlet"]) {
    const { type } = artStats[artType];
    const valueOf = {
      [hp_]: 46.6,
      [er]: 51.8,
      [hydroD]: 46.6,
      [cRate]: 31.1,
      [cDmg]: 61.2
    };
    ATTRs[type] += valueOf[type];
  }
}

function addArtSets(ATTRs, bonuses, artSets) {
  for (const set of artSets) {
    if (!set) continue;
    if (set === "2P TotM") {
      ATTRs[hp_] += 20;
    } else if (set === "2P HoD") {
      ATTRs[hydroD] += 15;
    } else if (set === "2P NO") {
      bonuses.EB += 20;
    } else {
      ATTRs[er] += 20;
      if (set.slice(0, 1) === "4") {
        const bnValue = ATTRs[er] / 4;
        bonuses.EB += Math.min(round2(bnValue), 75);
      }
    }
  }
}

function addMods(ATTRs, bonuses, rdMult, mods) {
  for (const type of Object.keys(mods)) {
    if ([hp_, er, hydroD].includes(type)) {
      ATTRs[type] += mods[type];
    } else if (type === hydroRd) {
      rdMult[type] += mods[type];
    } else {
      bonuses[type] += mods[type];
    }
  }
}

function calcResMult(mods) {
  let result = 10 - mods[hydroRd];
  result /= 100;
  if (result < 0) {
    result = 1 - result / 2;
  } else if (result >= 0.75) {
    result = 1 / (4 * result + 1);
  } else {
    result = 1 - result;
  }
  return result;
}

export function calculate(char, wp, artStats, artSets, bnEr, mods) {
  const ATTRs = {
    "Base HP": 0,
    HP: 0,
    [hp_]: 0,
    "Base ATK": 0,
    ATK: 0,
    "ATK%": 0,
    [cRate]: 5,
    [cDmg]: 50,
    [er]: 100 + bnEr,
    [hydroD]: 0
  };
  const bonuses = {
    CA: 0,
    ES: 0,
    EB: 0
  };
  const rdMult = {
    [hydroRd]: 0
  };
  addCharStats(ATTRs, char);
  addWpStats(ATTRs, bonuses, wp);
  addArtStats(ATTRs, artStats);
  addMods(ATTRs, bonuses, rdMult, mods);
  addArtSets(ATTRs, bonuses, artSets);
  for (const type of ["CA", "ES", "EB"]) {
    bonuses[type] += ATTRs[hydroD];
  }
  const resMult = calcResMult(mods);

  return [ATTRs, bonuses, resMult];
}

export function rollSim(
  avaHpRolls,
  ATTRs,
  rollConfig,
  maxRolls,
  char,
  bonuses,
  resMult
) {
  const result = {
    states: [],
    hpRS: [],
    cRateRS: [],
    cDmgRS: [],
    dmg: []
  };
  // const HP = ATTRs["Base HP"] + ATTRs.HP;
  const mult = {};
  ["CA", "ES", "EB"].forEach((type, i) => {
    const { baseMult, multType } = Yelan.actvTalents[i];
    const tlMult = (baseMult * TalentLvMults[multType][char[type]]) / 100;
    mult[type] = tlMult * (1 + bonuses[type] / 100) * 0.5 * resMult;
  });

  for (let i = 0; i <= avaHpRolls; i++) {
    const state = {
      [hp_]: ATTRs[hp_],
      [cRate]: ATTRs[cRate],
      [cDmg]: ATTRs[cDmg]
    };
    const rolls = {
      [hp_]: [],
      [cRate]: [],
      [cDmg]: []
    };
    for (let j = 0; j < i; j++) {
      const hp = rollStat(rollConfig, hp_);
      state[hp_] += hp;
      rolls[hp_].push(hp);
    }
    for (let k = 0; k < maxRolls - i; k++) {
      const cR = rollStat(rollConfig, cRate);
      if (state[cRate] < state[cDmg] / 2 && state[cRate] + cR < 100) {
        state[cRate] += cR;
        rolls[cRate].push(cR);
      } else {
        const cD = rollStat(rollConfig, cDmg);
        state[cDmg] += cD;
        rolls[cDmg].push(cD);
      }
    }
    result.states.push(state);
    result.hpRS.push(rolls[hp_]);
    result.cRateRS.push(rolls[cRate]);
    result.cDmgRS.push(rolls[cDmg]);
    const thisDmg = {};
    for (const tl of Yelan.actvTalents) {
      const thisHP = ATTRs["Base HP"] * (1 + state[hp_] / 100) + ATTRs.HP;
      const crit = 1 + (state[cRate] / 100) * (state[cDmg] / 100);
      thisDmg[tl.type] = Math.round(thisHP * mult[tl.type] * crit);
    }
    result.dmg.push(thisDmg);
  }
  return result;
}

function rollStat(rollConfig, type) {
  if (rollConfig[type] === "Random") {
    return +rollValues[type][getRandomN(0, 3)];
  }
  return +rollConfig[type];
}
