import produce from "immer";
import { useState } from "react";
import acro from "../convertAcronym";
import { TabBar, VStack } from "./styles";

const SetSelect = ({ artSets, i, setArtSets }) => {
  const changeSet = (e) => {
    setArtSets((prev) => {
      const { value } = e.target;
      if (i) {
        return [prev[0], value];
      } else {
        let second = null;
        if (value !== "4P EoSF") {
          second = prev[1];
          if (!second) {
            for (const elmt of sets.slice(1)) {
              if (elmt !== value) {
                second = elmt;
                break;
              }
            }
          }
        }
        return [value, second];
      }
    });
  };

  return (
    <div className="line-1">
      <p>Set</p>
      <select value={artSets[i]} onChange={changeSet}>
        {sets.slice(i).map((set) => (
          <option key={set}>{set}</option>
        ))}
      </select>
    </div>
  );
};

export default function ArtsConfig({
  artStats,
  artSets,
  setArtStats,
  setArtSets
}) {
  const [chosen, setChosen] = useState("Flower");
  const isFoP = ["Flower", "Plume"].includes(chosen);
  const maxRoll = isFoP || artStats[chosen]?.type === acro.hydroD ? 9 : 8;

  const mainStat = (e) =>
    setArtStats(
      produce((draft) => {
        const { value } = e.target;
        draft[chosen].type = value;
        if (value === acro.hp_ && draft[chosen].rolls === 9) {
          draft[chosen].rolls = 8;
        }
      })
    );
  const changeRolls = (e) =>
    setArtStats(
      produce((draft) => {
        draft[chosen].rolls = +e.target.value;
      })
    );

  return (
    <VStack className="px-2 py-3">
      <p>(All piece Level 20)</p>
      <SetSelect artSets={artSets} i={0} setArtSets={setArtSets} />
      {artSets[0] !== "4P EoSF" && (
        <SetSelect artSets={artSets} i={1} setArtSets={setArtSets} />
      )}
      <TabBar>
        {Object.keys(artStats).map((artType) => (
          <p
            key={artType}
            className={chosen === artType ? "active" : ""}
            onClick={() => setChosen(artType)}
          >
            {artType}
          </p>
        ))}
      </TabBar>
      <div className="mt-1">
        {!isFoP && (
          <div className="line-1">
            <p>Main Stat</p>
            <select value={artStats[chosen].type} onChange={mainStat}>
              {mainStatsOf[chosen].map((type) => (
                <option key={type}>{type}</option>
              ))}
            </select>
          </div>
        )}
        <div className={"line-1" + (!isFoP ? " mt-3" : "")}>
          <p>Good rolls (ER / HP% / Crit)</p>
          <select value={artStats[chosen].rolls} onChange={changeRolls}>
            {[...Array(maxRoll).keys()].map((_, i) => (
              <option key={i}>{i + 1}</option>
            ))}
          </select>
        </div>
      </div>
    </VStack>
  );
}

const { hp_ } = acro;

const mainStatsOf = {
  Sands: [hp_, acro.er],
  Goblet: [hp_, acro.hydroD],
  Circlet: [hp_, acro.cRate, acro.cDmg]
};

const sets = ["4P EoSF", "2P EoSF", "2P TotM", "2P HoD", "2P NO"];
