import { useEffect, useRef, useState } from "react";
import {
  CollapseContent,
  CollapseHeading,
  StyledCard,
  StyledCollapse
} from "./styles";

export const Pct = () => <span className="ml-1">%</span>;

export function Card({ children }) {
  return (
    <div className="p-2">
      <StyledCard>{children}</StyledCard>
    </div>
  );
}

export function ExpandableList({ headingList, contentList }) {
  const [expanded, setExpanded] = useState([]);
  return (
    <div>
      {headingList.map((heading, i) => (
        <div key={i} className={i ? "mt-2" : ""}>
          <CollapseHeading
            className={"mb-2p5" + (expanded[i] ? " active" : "")}
            onClick={() =>
              setExpanded((prev) => {
                const newEpd = [...prev];
                newEpd[i] = !newEpd[i];
                return newEpd;
              })
            }
          >
            {heading}
          </CollapseHeading>
          <Collapse in={Boolean(expanded[i])}>
            <CollapseContent>{contentList[i]}</CollapseContent>
          </Collapse>
        </div>
      ))}
    </div>
  );
}

function Collapse(props) {
  const [ref, height] = useGetHeight();
  return (
    <StyledCollapse
      className={"hide-sb" + (props.in ? " open" : "")}
      transition={`height ${Math.max(
        Math.min(Math.round(height) / 2, 300),
        150
      )}ms ease-in-out`}
      openHeight={height + "px"}
    >
      <div ref={ref}>{props.children}</div>
    </StyledCollapse>
  );
}

function useGetHeight() {
  const ref = useRef();
  const [height, setHeight] = useState(0);
  useEffect(() => {
    const resizeObserver = new ResizeObserver((entries) => {
      for (let entry of entries) {
        if (entry.contentBoxSize) {
          const contentBoxSize = Array.isArray(entry.contentBoxSize)
            ? entry.contentBoxSize[0]
            : entry.contentBoxSize;
          setHeight(contentBoxSize.blockSize);
        } else {
          setHeight(entry.contentRect.height);
        }
      }
    });
    const elmt = ref.current;
    resizeObserver.observe(elmt);
    return () => resizeObserver.unobserve(elmt);
  }, []);
  return [ref, height];
}
