import { useState } from "react";
import { processNumInp } from "../helpers";
import { StyledNotes } from "./styles";

export default function Notes() {
  const [open, setOpen] = useState(false);
  const [values, setValues] = useState([0, 0]);
  const diff = values[1] - values[0];
  const pctDiff = Math.floor((diff / values[0]) * 10000) / 100;

  return (
    <StyledNotes className="flex align-start">
      <div className="handle mt-3" onClick={() => setOpen(!open)}>
        Notes
      </div>
      <div className={"content_wrapper" + (open ? " open" : "")}>
        <div className="content">
          <div className="flex align-center">
            <h3 className="mx-auto">Notes</h3>
            <span className="pointer" onClick={() => setOpen(false)}>
              <b>(X)</b>
            </span>
          </div>
          <textarea className="mt-2" rows={10} />
          <div className="mt-1 flex-wrap align-center">
            <p className="mt-2 mr-2">
              <b>Compare</b>
            </p>
            <div className="mt-2 flex align-center">
              <input
                value={values[0]}
                onChange={(e) =>
                  setValues((prev) => [
                    processNumInp(e.target.value, prev[0], 9999999),
                    prev[1]
                  ])
                }
              />
              <p className="mx-2">vs.</p>
              <input
                value={values[1]}
                onChange={(e) =>
                  setValues((prev) => [
                    prev[0],
                    processNumInp(e.target.value, prev[0], 9999999)
                  ])
                }
              />
            </div>
          </div>
          <p className="mt-3">
            Result:{" "}
            {values.includes(0) ? "Enter 2 values" : diff + ` (${pctDiff}%)`}
          </p>
        </div>
      </div>
    </StyledNotes>
  );
}
