import produce from "immer";
import { useEffect } from "react";
import acro from "../../convertAcronym";
import { getRandomN, processNumInp, round2 } from "../../helpers";
import { Pct } from "../Common";
import { VStack, ErRollConfig } from "../styles";

const avaValues = ["4.53", "5.18", "5.83", "6.48"];
const { er } = acro;

export default function ErRoll({ avaRolls, rollConfig, setRollConfig }) {
  const { rolls, value, total } = rollConfig[er];
  const min = avaRolls.total - 38;
  const minRolls = Math.max(min, 0);

  useEffect(() => {
    if (min >= 0 && rollConfig[er].rolls < min) {
      setRollConfig(
        produce((draft) => {
          draft[er].rolls = min;
          calcTotal(draft, draft[er].value);
        })
      );
    }
  }, [min]);

  const changeRolls = (e) =>
    setRollConfig(
      produce((draft) => {
        draft[er].rolls = processNumInp(e.target.value, rolls, avaRolls.er);
        calcTotal(draft, draft[er].value);
      })
    );
  const ensureMinRolls = (e) => {
    if (e.target.value < minRolls) {
      setRollConfig(
        produce((draft) => {
          draft[er].rolls = minRolls;
          calcTotal(draft, draft[er].value);
        })
      );
    }
  };
  const changeRollVal = (e) =>
    setRollConfig(
      produce((draft) => {
        const { value } = e.target;
        draft[er].value = value;
        calcTotal(draft, value);
      })
    );
  const genRandomTotal = () =>
    setRollConfig(
      produce((draft) => {
        draft[er].value = "Random";
        draft[er].total = round2(getRandomTotal(draft[er].rolls));
      })
    );

  return (
    <ErRollConfig>
      <h4>Energy Recharge:</h4>
      <VStack className="mt-2 pt-2 pl-2 pr-1">
        <p>
          Min. rolls required: <b>{minRolls}</b>
        </p>
        <p>
          Max. rolls available: <b>{avaRolls.er}</b>
        </p>
        <div className="line-1">
          <p>Number of rolls</p>
          <input value={rolls} onChange={changeRolls} onBlur={ensureMinRolls} />
        </div>
        <div className="flex-wrap">
          <div className="line-1">
            <p>Roll value</p>
            <select value={value} onChange={changeRollVal}>
              {avaValues.concat("Random").map((val) => (
                <option key={val}>{val}</option>
              ))}
            </select>
            <Pct />
          </div>
          <button
            className={"ml-3" + (rolls ? "" : " unavailable")}
            onClick={genRandomTotal}
          >
            Random
          </button>
        </div>
        <p>
          Total ER Bonus: <b>{total}%</b>
        </p>
      </VStack>
    </ErRollConfig>
  );
}

function calcTotal(draft, value) {
  if (value === "Random") {
    draft[er].total = round2(getRandomTotal(draft[er].rolls));
  } else {
    draft[er].total = round2(draft[er].value * draft[er].rolls);
  }
}

function getRandomTotal(limit) {
  let result = 0;
  for (let i = 0; i < limit; i++) {
    result += +avaValues[getRandomN(0, 3)];
  }
  return result;
}
