import styled from "styled-components";
import { colors } from "../theme";

export const Span = styled(({ color, b, ...props }) => <span {...props} />)`
  color: ${({ color }) => colors[color] || color};
  font-weight: ${(props) => (props.b ? 700 : 400)};
`;

export const LBlue = (props) => <Span color="blue" {...props} />;
export const Blue = (props) => <Span color="blue" b {...props} />;

export const Settings = styled.div`
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  .static_config {
    max-width: 23rem;
    display: flex;
    flex-direction: column;
    & > div {
      max-height: 34.325rem;
      overflow: auto;
    }
  }
`;

export const StyledCard = styled.div`
  height: 100%;
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: ${colors.lightYellow};
  & > *:not(:last-child) {
    margin-bottom: 1rem;
  }
`;

export const CollapseHeading = styled.p`
  padding: 0.25rem 1rem 0;
  background: ${colors.lightGreen};
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.6;
  cursor: pointer;
  &.active {
    background: ${colors.lightRed};
  }
`;

export const CollapseContent = styled.div`
  padding: 0 0.5rem;
  background-color: ${colors.lightYellow};
`;

export const StyledCollapse = styled.div`
  height: 0;
  transition: ${(props) => props.transition};
  &.open {
    height: ${(props) => props.openHeight};
  }
`;

export const VStack = styled.div`
  display: flex;
  flex-direction: column;
  & > *:not(:last-child) {
    margin-bottom: ${(props) => props.space || "1rem"};
  }
`;

export const TabBar = styled.div`
  display: flex;
  cursor: pointer;
  & > p {
    padding: 0.5rem 0.375rem 0.25rem;
    background-color: ${colors.lightGreen};
  }
  & > p.active {
    background-color: ${colors.lightRed};
  }
  & > p:not(:last-child) {
    border-right: 1px solid ${colors.lightYellow};
  }
`;

export const ErRollConfig = styled.div`
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 0.75rem 0;
`;

export const StyledNotes = styled.div`
  position: fixed;
  top: 1rem;
  right: 0rem;
  z-index: 2;
  .handle {
    padding: 0.5rem;
    border: 1px solid black;
    border-radius: 0.5rem 0 0 0.5rem;
    border-right: none;
    background-color: ${colors.lightRed};
    cursor: pointer;
  }
  .content_wrapper {
    width: 0;
    border-radius: 0.5rem 0 0 0.5rem;
    background-color: #928afc;
    overflow: hidden;
    transition: 200ms ease-in-out;
  }
  .content_wrapper.open {
    width: calc(100vw - 1rem);
    border: 1px solid black;
    border-right: none;
    @media (min-width: 400px) {
      width: 350px;
    }
  }
  .content {
    width: calc(100vw - 1rem);
    padding: 1rem;
    @media (min-width: 400px) {
      width: 350px;
    }
  }

  h3 {
    text-align: center;
  }
  textarea {
    width: 100%;
    padding: 0.5rem;
    font-size: 1rem;
    resize: vertical;
  }
  input {
    width: 6rem;
    padding: 0.25rem;
    font-size: 1rem;
  }
`;
